import React, { useState } from "react";
import { HashLink } from "react-router-hash-link";

import CloseBtn from "../closeBtn.svg";

const Header = () => {
  const [open, setOpen] = useState(false);

  

  return (
    <div className="headerContainer">
      <div className="headerWrapper">
        <button className="burgerBtn" onClick={() => setOpen(!open)}>
          <img
            src={open ? CloseBtn : "assets/burgerBtn.svg"}
            alt=""
            className="burgerBtnItem"
          />
        </button>

        <div className="logo">
          <HashLink smooth to="/">
            <img src="assets/logo-blck.svg" alt="logo" />
          </HashLink>
        </div>
        <div className="linksDesktop">
          <ul>
            <li>
              <HashLink smooth to="/#services">
                Services
              </HashLink>
            </li>
            <li>
              <HashLink smooth to="/#technologies">
                Technologies
              </HashLink>
            </li>
            <li>
              <HashLink smooth to="/#aboutus" >
                About
              </HashLink>
            </li>
            <li>
              <HashLink smooth to="/#contactus" id="high1">
                Get into Touch
              </HashLink>
            </li>
          </ul>
        </div>
        <div className={open ? "linksMobile" : "remover"}>
          <ul>
            <li>
              <HashLink smooth to="/#services"  onClick={() => setOpen(!open)}>
                Services
              </HashLink>
            </li>
            <li>
              <HashLink smooth to="/#technologies"  onClick={() => setOpen(!open)}>
                Technologies
              </HashLink>
            </li>
            <li>
              <HashLink smooth to="/#aboutus" onClick={() => setOpen(!open)}>
                About
              </HashLink>
            </li>
            <li id="highLI">
              <HashLink smooth to="/#contactus" id="high1"  onClick={() => setOpen(!open)}>
                Get into Touch
              </HashLink>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Header;
